
.form{
    width: 95%;
    margin: 0 0 0 5%;
    border-radius: 5rem;
    padding: 0.5rem;
    /* border: 0.12rem var(--main-color-brown) solid; */
    background: #fdf2da;
}

.grid-box{
    display: grid;
    grid-template-columns: [x] 50% [y] 50%;
}

.title-form{
    text-align: center;
}

.form-checkbox{
    width: 20rem;
    text-align: center;
    margin:auto
}

.checkbox{
    margin-bottom: 0.3rem;
}

.formular-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
}

.enable-display{
    display: none;
}

.anchor-data{
    color: black;
    display: inline;
    text-decoration: underline;
    cursor: pointer;
}
