.menu-container {
    position: fixed;
    top: 0;
    width: 100%;
    background-color: var(--main-color);
  }
  
  .homePage-menuContainer {
    background-color: var(--main-color-orange);
    display: grid;
    grid-template-columns: [first] 22.5% [second] 55% [third] 22.5%;
    position: relative;
    box-shadow: 0px 5px 5px grey;
  }
  
  .homePage-item--menu {
    display: block;
  }
  
  .homePage-menu {
    margin: 0;
    padding: 1rem 3rem;
  
    list-style: none;
  
    display: flex;
    grid-column: second;
    justify-content: space-evenly;
  
  }
  
  .homePage-item {
    text-align: center;
  }
  
  .homePage-item-button--dark {
    border: none;
    border-radius: 0.5rem;
    padding: 0.5rem;
    text-transform: capitalize;
    font-size: 1.2rem;
    color: azure;
    background-color: var(--main-color-green);
  }
  
  .homePage-item-button--dark:hover {
    cursor: pointer;
  }
  
  .homePage-item--logo {
    grid-column: first;
    margin: 0.5rem;
  }
  
  .homePage-logo {
    width: 90%;
    margin: auto 0;  
    margin-top: 2rem;
  }
  
  .homePage-item-button {
    border: none;
    border-radius: 0.5rem;
    padding: 0.5rem;
    text-transform: capitalize;
    background: none;
    font-size: 1.2rem;
  }
  
  .homePage-item-button:hover {
    background-color: var(--main-color-green);
    color: azure;
    transition: background-color 0.3s linear;
    cursor: pointer;
  }
  
  
  .homePage-item--dot {
    height: 11rem;
    width: 11rem;
    margin-top: -2rem;
    margin-left: -0.1rem;
    padding: 0.5rem;
    background-color: var(--main-color-orange);
    border-radius: 50%;
    position: absolute;
    display: flex;
    justify-content: center;
  }
  