.quotes-container{
    margin-top: 5rem;
    height: 14rem;
    background-color: var(--main-color-green);
    display: flex;
    justify-content: center;
  }

  .quote-container{
    margin: 3rem 2.5rem;
    width: 15%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

.quote{
    text-align: start;
    /* font-family: 'Oooh Baby', cursive; */
    font-size: 1rem;
    color: var(--main-color-grey);
    display: block;
}

.author{
    /* font-family: 'Oooh Baby', cursive; */
    color: var(--main-color-grey);
    text-align: end;
    font-weight: 100;
    font-size: 0.9rem;
    padding-right: 0.3rem;
}

.button-next{
    border: none;
    background-color: transparent;
    cursor: pointer;
    height: 100%;
}

.button-container{
    width: 10rem;
}

.image-next{
    width: 5rem;
    margin: 50%;
}



