.footer {
    background-color: var(--main-color-brown);
    color: azure;
    margin-top: 3rem;
    background-image: url('./images/edited-logo1.webp');
    background-repeat: no-repeat;
    background-size: 15%;
    background-position-x: 30%;
    background-position-y: center;
  }
  
  .footer-container {
    display: flex;
    flex-direction: column;
    justify-items: center;
    font-size: 1.5rem;
    text-transform: uppercase;
    text-align: center;
    padding-top: 0.5rem;
    align-items: center;
  
  }
  
  .footer-list {
    font-size: 1rem;
    text-transform: none;
    list-style: none;
    text-shadow: none;
    align-items: center;
    padding: 0;
  }
  
  .footer-item {
    padding: 0.4rem 0;
    display: flex;
    justify-content: center;
    height: 1.5rem;
  }
  
  .footer-image{
    height: 90%;
    margin: auto 0;  
    margin-right: 0.5rem;
  }
  
  .footer-title{
    color: var(--main-color-orange);
  }
  
  a {
    color: azure;
  }