.info-container{
    padding-top: 10rem;
}

.info-number{
    border-radius: 100%;
    border: 0.1rem solid black;
    width: 3rem;
    text-align: center;
    font-size: 2rem;
    margin: auto;
    margin-bottom: 1rem; 
}

.info{
    display: flex;
    margin: auto;
    margin-top: 0;
    width: 70%;
}

.info-item{
    padding: 1rem;
    font-size: 1rem;
}

.info-click{
    width: auto;
    font-size: 1rem;
}

.info-mail{
    width: 70%;
    margin: auto;
}

.info-text{
    display: block;
    width: 50%;
    margin: 1rem auto;
    height: 5rem;
    padding: 0.5rem;
    border-radius: 0.5rem;
}

.info-text-click{
    display: block;
    margin: 1rem auto;
}

.info-title{
    text-align: center;
}

.info-input{
    display: block;
    width: 75%;
    margin: 1rem auto;
    padding: 0.5rem;
    border-radius: 0.5rem;
}
